import React, {Component} from 'react';
export default class Secret extends Component{
  render(){
    return(
      <div>
      <p>This is a Super secret area</p>

      <div>
      <button onClick={this.props.auth.logout}>
      Logout
      </button>
      </div>
          </div>

  )
  }
}



